.container .nav-left {
  height: calc(100vh);
  background: #001529;
}
.container .main {
  height: calc(100vh);
  overflow: auto;
}
.container .content {
  position: relative;
  padding: 20px;
  background-color: #f5f5f5;
}
.sample-header {
  height: 70px;
  line-height: 70px;
  padding: 0 50px;
  background-color: blue;
  text-align: right;
}
.sample-header a {
  margin-left: 40px;
}
.sample-header span {
  color: #c8c8c8;
}
.sample-header img {
  width: 50px;
}
.sample-header h1 {
  color: #c8c8c8;
  display: inline;
  vertical-align: middle;
  margin-left: 20px;
}
